export default class extends React.Component {
    render() {
        const { products } = this.props

        return (
            <ul className="products">
                {
                    products.map((product) => (
                    <li className="product">
                        {product.name}
                    </li>
                )) }
                
                <style jsx>{`
                    .products {
                        margin: 0px;
                        padding: 0px;
                        display: flex;
                        flex-wrap: wrap;
                    }

                    .product {
                        background-color: white;
                        padding: 1rem;
                        margin-right: 2%;
                        -webkit-box-shadow: 3px 3px 6px -2px rgba(23,23,23,0.3);
                        -moz-box-shadow: 3px 3px 6px -2px rgba(23,23,23,0.3);
                        box-shadow: 3px 3px 6px -2px rgba(23,23,23,0.3);
                        width: 31%;
                        margin-bottom: 2%;
                    }
                `}</style>
            </ul>
        )
    }
}